.App {
  text-align: center;
}
.small-screen-text{
  font-size: 30px;
  font-size: 26px;
  font-weight: bold;
  color: #0000007a;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: #f4f0ff;
  margin: 50px;
}
@media (max-width: 1200px) {
  .main {
    display: none;
  }
}
@media (min-width: 1200px) {
  .small-screen-text{
    display: none;
  }
  .main {
    display: flex;
  }
}
